import React, { useState } from "react";
import { getAuth, signInWithPopup, GoogleAuthProvider, OAuthProvider } from "firebase/auth";
import { useNavigate } from "react-router-dom";
import { auth } from "../firebase"; // Firebase  auth  


const LoginPage = () => {
  const [nickname, setNickname] = useState("");
  const navigate = useNavigate();

  // Google  
  const handleGoogleLogin = async () => {
    const provider = new GoogleAuthProvider();
    try {
      const result = await signInWithPopup(auth, provider);
      const user = result.user;
      console.log("Google  :", user);
      checkNickname(user); //  
    } catch (error) {
      console.error("Google  :", error);
    }
  };

  // Apple  
  const handleAppleLogin = async () => {
    const provider = new OAuthProvider("apple.com");
    try {
      await signInWithPopup(auth, provider);
    } catch (error) {
      console.error("Apple  :", error);
    }
  };

  //  
  const checkNickname = async (user) => {
    try {
      const response = await fetch(`https://hongs.onrender.com/api/check-nickname?uid=${user.uid}`);
      const data = await response.json();
      console.log("  :", data);

      if (data.hasNickname) {
        // setNickname(response.data.nickname); // nickname  
        setNickname(data.nickname);

        //    
        navigate("/main");
      } else {
        console.error("  .");
        //     
        navigate("/signup");
      }
    } catch (error) {
      console.error("  :", error);
    }
  };

  return (
    <div className="flex flex-col items-center justify-center h-screen">
      <h1 className="text-2xl font-bold mb-6"></h1>
      <button
        className="bg-blue-500 text-white px-6 py-2 rounded-md mb-4"
        onClick={handleGoogleLogin}
      >
         
      </button>
      {/* <button
        className="bg-gray-800 text-white px-6 py-2 rounded-md"
        onClick={handleAppleLogin}
      >
          
      </button> */}
    </div>
  );
};

export default LoginPage;
