// src/components/ChapterGrid.js
import React, { useState, useEffect } from "react";
import bibleData from "../data/data.json"; // JSON  

const ChapterGrid = ({ completed, onChapterClick }) => {
  const [gridItems, setGridItems] = useState([]); //   

  useEffect(() => {
    const loadBibleData = () => {
      try {
        const items = [];

        // JSON     
        const introItem = bibleData[0]; //    
        const isEmpty =
          !introItem["1"] &&
          !introItem["2"] &&
          !introItem["3"] &&
          !introItem["4"] &&
          !introItem["5"] &&
          !introItem["6"]; // 7    

        if (isEmpty) {
          // 7      ""   
          items.push({
            merged: true, //  
            content: introItem[""], //   
            index: 0, //    
          });
        }

        //   
        bibleData.slice(1).forEach((week, weekIndex) => {
          items.push({ merged: false, content: week[""], index: weekIndex * 7 + 1 }); //   :  
          items.push({ merged: false, content: week["1"], index: weekIndex * 7 + 2 });
          items.push({ merged: false, content: week["2"], index: weekIndex * 7 + 3 });
          items.push({ merged: false, content: week["3"], index: weekIndex * 7 + 4 });
          items.push({ merged: false, content: week["4"], index: weekIndex * 7 + 5 });
          items.push({ merged: false, content: week["5"], index: weekIndex * 7 + 6 });
          items.push({ merged: false, content: week["6"], index: weekIndex * 7 + 7 });
        });

        setGridItems(items.filter((item) => item !== null)); // null  
      } catch (error) {
        console.error("   :", error);
      }
    };

    loadBibleData();
  }, []);

  return (
    <div className="p-4">
      <div className="grid grid-cols-7 gap-4">
        {gridItems.map((item, index) =>
          item.merged ? (
            //              
            <div
              key={index}
              className={`p-4 rounded-lg border text-center col-span-7 font-bold cursor-pointer ${
                completed[0] ? "bg-green-100" : "bg-gray-100"
              }`}
              onClick={() => onChapterClick(0, item.content)} //  ,   index 0 
            >
              {item.content}
            </div>
          ) : (
            //  
            <div
              key={index}
              className={`p-4 rounded-lg border ${
                completed[item.index] ? "bg-green-100" : "bg-gray-100"
              } cursor-pointer text-center`}
              onClick={() => onChapterClick(item.index, item.content)} //    
            >
              {item.content || " "}
            </div>
          )
        )}
      </div>
    </div>
  );
};

export default ChapterGrid;
