import React, { useState } from "react";
import { getAuth } from "firebase/auth";
import { useNavigate } from "react-router-dom";

const SignupPage = () => {
  const [nickname, setNickname] = useState("");
  const [isDuplicate, setIsDuplicate] = useState(false);
  const navigate = useNavigate();
  const auth = getAuth();

  const handleNicknameChange = (e) => setNickname(e.target.value);

  //   
  const checkNickname = async () => {
    try {
      const response = await fetch("https://hongs.onrender.com/api/check-nickname-duplicate", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ nickname }),
      });

      const data = await response.json();
      setIsDuplicate(!data.available); //   
    } catch (error) {
      console.error("   :", error);
    }
  };

  //  
  const handleSignup = async () => {
    if (isDuplicate || !nickname.trim()) {
      alert("  .");
      return;
    }
  
    try {
      const user = auth.currentUser; // Firebase    
      await fetch("https://hongs.onrender.com/api/signup", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ uid: user.uid, nickname }),
      });
  
      //     
      navigate("/main");
    } catch (error) {
      console.error(" :", error);
    }
  };
  
  

  return (
    <div className="flex flex-col items-center justify-center h-screen">
      <h1 className="text-2xl font-bold mb-6"> </h1>
      <input
        type="text"
        value={nickname}
        onChange={handleNicknameChange}
        placeholder=" "
        className="border px-4 py-2 rounded-md mb-4"
      />
      <button
        onClick={checkNickname}
        className="bg-gray-500 text-white px-4 py-2 rounded-md mb-4"
      >
          
      </button>
      {isDuplicate && <p className="text-red-500"> .</p>}
      <button
        onClick={handleSignup}
        className="bg-blue-500 text-white px-6 py-2 rounded-md"
      >
          
      </button>
    </div>
  );
};

export default SignupPage;
